.navbar-item{
    margin-right: .6rem;
    margin-left: .6rem;
    cursor: pointer;
    color: black;
}

.search-bar{
    margin-left: .15rem;
    background-color: transparent;
    display: flex;
    align-items: center;
    outline: none;
    border-color: black;
    color: black;
    border-top: none;
    border-left: none;
    border-right: none;
    font-size: 1rem;
    padding: .1rem;
}
.navbar-container{
    height: 3.5rem;
    width: 100%;
    top: 0;
    display: flex;
    align-items: center;
    position: sticky;
    margin-bottom: 1rem;
    box-shadow: .2rem 0 .5rem rgba(26, 26, 26, 0.201);
}

